<template>
  <div id="view-employee">
    <h3>View-employee</h3>
    <ul class="collection with-header">
      <li class="collection-item collection__header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">
        <strong>Employee ID # </strong> {{employee_id}}
      </li>
      <li class="collection-item">
        <strong>Department </strong> {{dept}}
      </li>
      <li class="collection-item">
        <strong>Position: </strong> {{position}}
      </li>
    </ul>
    <router-link class="btn black"
                 to="/">
      Back
    </router-link>
    <button class="btn red"
            @click="deleteUser">
      Delete
    </button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>

  import db from './firebase.init';

  export default {
    name: "view-employee",
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
      }
    },

    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(query => {
          query.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            })
          })
        })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(query => {
          query.forEach(doc => {

              this.employee_id = doc.data().employee_id,
              this.name = doc.data().name,
              this.dept = doc.data().dept,
              this.position = doc.data().position
          })
        })
      },
      deleteUser() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
          .get()
          .then(query => {
            query.forEach(doc => {
              doc.ref.delete();
              this.$router.push('/')
            })
          })
      }
    }
  }
</script>

<style scoped>

</style>



