<template>
  <div id="dashboard">
    <h3>Dashboard</h3>
    <ul class="collection with-header">
      <li class="collection-item collection__header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in empployees"
          v-bind:key="employee.id"
          class="collection-item">
        <div class="chip">{{employee.dept}}</div>
        {{employee.employee_id}}: {{employee.name}}

        <router-link class="secondary-content"
                     v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebase.init';

  console.log(db);
  export default {
    name: "dashboard",
    data() {
      return {
        empployees: []
      }
    },
    created() {
      db.collection('employees')
        .orderBy('dept')
        .get()
        .then(query => {
          console.log(query);
          query.forEach(doc => {
            const data = {
              'id': doc.id,
              'employee_id': doc.data().employee_id,
              'name': doc.data().name,
              'dept': doc.data().dept,
              'position': doc.data().position,
            };

            this.empployees.push(data);
            console.log(this.empployees);

          })
        })
    }
  }
</script>

<style scoped>

</style>



