<template>
  <div id="edit-employee">
    <h3>edit-employee</h3>
    <h3>{{name}}</h3>
    <form @submit.prevent="updateUser" class="form">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="employee_id" disabled/>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="name" required/>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="dept" required/>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="position" required/>
        </div>
      </div>
      <div class="row">
        <button type="submit" class="btn">
          Update
        </button>
        <router-link class="btn black"
                     to="/">
          Cancel
        </router-link>

      </div>
    </form>
  </div>
</template>

<script>

  import db from './firebase.init';

  export default {
    name: "edit-employee",
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
      }
    },

    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(query => {
          query.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            })
          })
        })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(query => {
          query.forEach(doc => {
            this.employee_id = doc.data().employee_id,
              this.name = doc.data().name,
              this.dept = doc.data().dept,
              this.position = doc.data().position
          })
        })
      },

      updateUser() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(query => {
          query.forEach(doc => {
              doc.ref.update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
                .then(() =>{
                  this.$router.push({name: 'view-employee', params: {employee_ide: this.employee_id}})
                })
          })
        })
      }
    }
  }

</script>

<style scoped>

</style>



